.app-wrapper {
    display: grid;

    grid-template-areas:
    'header header'
    'sidebar-nav content';

    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;

    width: 1200px;
    margin: auto;
}

.app-wrapper-content {
    grid-area: content;
    background-color: cornflowerblue;
}